import s from './template.scss';
import { checkEnv } from './../../utils/scan';

export const nodeMap = {
	phone: `${s.phone}`,
	verificationCode: `${s.verificationCode}`,
	antiFakeCode: `${s.antiFakeCode}`,
	scan: `${s.scan}`,
	sendVerificationCode: `${s.sendVerificationCode}`,
	submit: `${s.submit}`
};

const prefix = 'by-health-points';

export default (disabledPhone, verifyPhone, hidePhone, data) =>{
	return `<div class="${prefix}-wrap">
        <div class="${prefix}-antifakecode-item">
            <input class="${prefix}-antifakecode" type="tel" id="${s.antiFakeCode}" value="${data.antiFakeCode || ''}" maxlength="16" placeholder="16" />
            ${(checkEnv() === 1 || checkEnv() === 2 || checkEnv() === 3) ? `<button class="${prefix}-button-scan" id="${s.scan}"></button>` : ''}
        </div>
        ${hidePhone ? '' : `<div class="${prefix}-phone-item">
            <input class="${prefix}-phone" type="tel" value="${data.phone || ''}" ${disabledPhone ? 'disabled' : ''} id="${s.phone}" maxlength="11" placeholder="" />
        </div>`}
        ${verifyPhone ? `<div class="${prefix}-verificationcode-item">
            <input class="${prefix}-verificationcode" type="tel" id="${s.verificationCode}"  value="${data.verificationCode || ''}" maxlength="4" placeholder="" />
            <button class="${prefix}-button-sendverificationcode" id="${s.sendVerificationCode}"></button>
        </div>` : ''}
        <div class="${prefix}-submit-item">
            <button class="${prefix}-button-submit" id="${s.submit}"></button>
        </div>
    </div>`;
};
